@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900);
html {
  height: 100%;
  min-height: 600px;
}
body {
  background-color: #ffffff;
  margin: 0;
  padding: 0;
  border: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  min-height: 100%;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  overflow-x: hidden;
}
#app {
  width: 100%;
  margin: 0 auto;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-flex: 1;
      -ms-flex: 1 0 auto;
          flex: 1 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
}
.main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-flex: 1;
      -ms-flex: 1 0 auto;
          flex: 1 0 auto;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.container-produto {
  width: 100%;
  max-width: 1110px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
}
@media screen and (max-width: 1000px) {
.container-produto {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
}
.content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding: 20px;
}
.nome-produto {
  font-family: "Roboto", sans-serif;
  font-size: 48px;
  font-weight: 400;
  letter-spacing: -1.2px;
  color: #000;
}
.info-produto {
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #828282;
}
.descricao-produto {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #000000;
}
.preco-produto {
  font-family: "Roboto", sans-serif;
  font-size: 48px;
  line-height: 0;
  font-weight: 400;
  color: #27AE60;
  margin-bottom: 10px;
  margin-top: 48px;
}
.condicao-produto {
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  font-weight: 400;
  margin-bottom: 40px;
  color: #000000;
}
.slug {
  font-family: "Roboto", sans-serif;
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 400;
  color: #656667;
  margin-bottom: -30px;
}
.center {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
button {
  -webkit-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
  cursor: pointer;
}
button:active {
  outline: none !important;
}
button:focus {
  outline: 0 !important;
}
.btn-comprar {
  border-radius: 4px;
  height: 70px;
  font-family: "Roboto", sans-serif;
  font-size: 26px;
  font-weight: 400;
  text-transform: uppercase;
  border: transparent;
  background-color: #27AE60;
  color: #ffffff;
  -webkit-box-shadow: 0px 4px 6px 0px rgba(0, 0, 0, 0.25);
  box-shadow: 0px 4px 6px 0px rgba(0, 0, 0, 0.25);
}
.btn-comprar:hover {
  margin-top: 2px;
  -webkit-box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.25);
  box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.25);
  background-color: #3db670;
}
.box-thumb {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding: 10px;
}
.thumb {
  max-width: 700px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding: 0;
}
.thumb li {
  background-color: #e8eaec;
  margin: 10px;
  padding: 10px;
  cursor: pointer;
  border-radius: 3px;
  -webkit-transition: all 0.1s ease;
  transition: all 0.1s ease;
  list-style: none;
}
.thumb li:hover {
  background-color: #d3d8dd;
}
.imagemGrande img {
  max-width: 500px;
  max-height: 500px;
  width: auto;
  height: auto;
  position: relative;
  float: left;
  display: block;
}
.active {
  background-color: #27AE60 !important;
}

























































































































